<template>
  <div class="site">
    <div class="site-s">
      <input type="text"
             v-model="inpValue"
             placeholder="请输入站点">
      <a-button type="primary"
                icon="search"
                @click="searchBtn">
      </a-button>
    </div>
    <div class="site-table">
      <div class="operatBtn">
        <a-button type="primary">
          <a-icon type="plus-circle" /> 添加
        </a-button>
        <a-select defaultValue="新闻"
                  style="width: 80px"
                  @change="handleChange">
          <a-select-option value="0">新闻</a-select-option>
          <a-select-option value="1">论坛</a-select-option>
        </a-select>
        <a-switch checkedChildren="可采集"
                  unCheckedChildren="未采集"
                  @change="statusChange" />
      </div>
      <Table :obj="{'columns': columns, 'tabdata': tabdata,
              'pagination': pagination, 'loading': loading}"
             @rowClick="childTable">
      </Table>
    </div>
    <div class="modal-tab">
      <a-modal title="Modal"
               v-model="visible"
               okText="确认"
               width="80%"
               :centered= true :afterClose= "modalClose"
               cancelText="取消">
        <a-table :columns="childCol"
                 :pagination="childPage"
                 :loading="childLoad"
                 :rowKey="record => record.id"
                 :dataSource="childTab"
                 @change="childTableChange">
          <div slot="status"
               slot-scope="text, record, index"
               @click.stop="bubbling">
            <a-switch checkedChildren="已采集"
                      unCheckedChildren="未采集"
                      :defaultChecked="text.status == '1'"
                      @change="childSwitch(text, record, index)" />
          </div>
          <div slot="checked"
               slot-scope="text, record, index"
               @click.stop="bubbling">
               <a-button type="primary" monitorLoading
                         @click="childCheck(text, record, index)">
                  监测
                </a-button>
            <!-- <a-switch checkedChildren="已监测"
                      unCheckedChildren="未监测"
                      :defaultChecked="text.status == '1'"
                      @change="childCheck(text, record, index)"/> -->
          </div>
          <div slot="remark"
             slot-scope="text, record, index"
             @click.stop="bubbling">
            <input :value="text.remark" type="text"
                    @keyup.enter="remarkHandler(text)">
          </div>
        </a-table>
      </a-modal>
    </div>
  </div>
</template>

<script>
import Table from '../../../common/table'
export default {
  name: 'Site',
  components: {
    Table
  },
  data () {
    return {
      visible: false,
      columns: [
        {
          title: '网站名称',
          dataIndex: 'webSite',
          width: '15%'
        }, {
          title: '网址',
          scopedSlots: { customRender: 'siteLink' },
          width: '20%'
        }, {
          title: '网站状态',
          scopedSlots: { customRender: 'status' },
          // customRender: (text, row, index) => {
          //   // console.log(text, row, index)
          //   if (text.status === 0) {
          //     return <a-switch checkedChildren="可采集" unCheckedChildren="未采集"/>
          //   } else {
          //     return <a-switch checkedChildren="可采集" unCheckedChildren="未采集" defaultChecked/>
          //   }
          // },
          customCell: function (record, rowIndex) {
          },
          width: '10%'
        }, {
          title: '网站类型',
          customRender: (text, row, index) => {
            return text.sort === 0 ? '新闻' : '论坛'
          },
          width: '15%'
        }, {
          title: '备注',
          scopedSlots: { customRender: 'remark' },
          // customRender: (text, row, index) => {
          //   return <input type="text"/>
          // },
          width: '30%'
        }
      ],
      childCol: [
        { title: '网站名称',
          dataIndex: 'web_name'
        },
        { title: '网址',
          dataIndex: 'url'
        },
        { title: '网站状态',
          scopedSlots: { customRender: 'status' }
        },
        { title: '监测状态',
          scopedSlots: { customRender: 'checked' }
          // customRender: (text, row, index) => {
          //   return text.checked === 0 ? '未监测' : '监测'
          // }
        },
        { title: '备注',
          scopedSlots: { customRender: 'remark' }
        }
      ],
      tabdata: [],
      childTab: [],
      pagination: {},
      childPage: {defaultCurrent: 1, current: 1},
      loading: false,
      childLoad: false,
      inpValue: '',
      searchObj: { page: 1, limit: 10 },
      PagingObj: { page: 1, limit: 10, status: '', checked: '' },
      childStatus: null,
      childChecked: null,
      monitorLoading: false
    }
  },
  mounted () {
    this.siteAll(this.searchObj)
    // this.switchBtn()
  },
  methods: {
    childTableChange (pagination) {
      this.PagingObj.page = pagination.current
      this.childTable(this.PagingObj)
    },
    childTable (parentPage) {
      var str = ''
      this.visible = true
      this.childLoad = true
      for (var key in parentPage) {
        str += `${key}=${parentPage[key]}&`
      }
      var newStr = str.slice(0, -1)
      let url = `${this.baseURL}/web/select_all?` + newStr
      this.$http.get(url).then(res => {
        console.log(res)
        if (res.status === 200) {
          var data = res.data.data
          var count = res.data.count
          var obj = {}
          obj.total = count
          this.childPage = obj
          this.childTab = data
          this.childLoad = false
          this.PagingObj.page = 1
        }
      })
    },
    // 类别查询
    handleChange (e) {
      this.searchObj.sort = e
      this.siteAll(this.searchObj)
    },
    // 状态查询
    statusChange (e) {
      this.searchObj.status = e - 0
      this.siteAll(this.searchObj)
    },
    // 所有站点查询
    siteAll (obj) {
      // page, sort, status, keyWord
      this.loading = true
      var str = ''
      obj.keyword = obj.keyword || ''
      obj.sort = obj.sort || '0'
      obj.status = obj.status || 0
      for (var key in obj) {
        str += `${key}=${obj[key]}&`
      }
      var newStr = str.slice(0, -1)
      let url = `${this.baseURL}/web_site/select_all?` + newStr
      this.$http.get(url).then(res => {
        if (res.status === 200) {
          var data = res.data.data
          var count = res.data.count
          var obj = {}
          obj.total = count
          this.pagination = obj
          this.tabdata = data
          this.loading = false
        }
      }, reject => {
        console.log(reject)
      })
    },
    // 找点查询按钮
    searchBtn () {
      this.searchObj.keyword = this.inpValue
      this.siteAll(this.searchObj)
    },
    // 子table表格的状态改变
    childSwitch (text) {
      console.log(text)
      var status = event.target.innerText === '已采集' ? 0 : 1
      this.childStatus = status
      let url = `${this.baseURL}/web/update?id=${text.id}&status=${status}`
      this.$http.get(url).then((res) => {
        console.log(res)
      }, (reject) => {
        console.log(reject)
      })
    },
    // 子表格的监测状态改变
    childCheck (text) {
      // var checked = event.target.innerText === '已监测' ? 0 : 1
      // this.childChecked = checked
      let url = `${this.baseURL}task/spidere?url=${text.url}`
      this.$http.get(url).then((res) => {
        console.log(res)
      }, (reject) => {
        console.log(reject)
      })
    },
    // 子表格备注提交
    remarkHandler (text) {
      let val = event.target.value
      console.log(val)
      let url = `${this.baseURL}/web/update?id=${text.id}&remark=${val}`
      this.$http.get(url).then((res) => {
        console.log(res)
      }, (reject) => {
        console.log(reject)
      })
    },
    // 空事件阻止冒泡
    bubbling () {
      // console.log(1111)
    },
    // 弹出框关闭
    modalClose () {
      this.childPage = {'current': 1}
    }
  }
}
</script>

<style lang="stylus" scoped>
.site {
  .site-s {
    height: 60px;
    line-height: 60px;
    text-align: center;

    input {
      height: 34px;
      border: 1px solid #ccc;
      text-indent: 0.2rem;
    }
  }

  .site-table {
    height: 60px;
    line-height: 60px;

    .operatBtn {
      padding-left: 30px;

      .add-sel {
        margin: 0 10px;
      }
    }
  }
}
</style>



